import { Flex, HTMLChakraProps, Text } from '@chakra-ui/react'
import { useNavigate } from 'react-router-dom'
import Icon from './Icon'
interface backProps extends HTMLChakraProps<'div'> {
  text?: string
  onClick?: () => void
}

export default function BackButton({ text, onClick, ...rest }: backProps) {
  const navigate = useNavigate()
  return (
    <Flex
      w="73px"
      h="32px"
      align="center"
      justifyContent="center"
      bg="bg_secondary"
      borderRadius="8px"
      border="1px solid"
      borderColor="border"
      cursor="pointer"
      fontWeight="500"
      onClick={() => {
        if (onClick) {
          onClick()
        } else {
          navigate(-1)
        }
      }}
      sx={{
        _hover: {
          p: {
            color: 'text_caption'
          },
          svg: {
            fill: 'text_caption'
          }
        }
      }}
      {...rest}
    >
      {text !== 'Back to DEX' && <Icon xlinkHref="#icon-icon_descending_nor" fontSize="16px" transform="rotate(90deg)" />}
      <Text lineHeight="1">{text ? text : 'Back'}</Text>
    </Flex>
  )
}
